import React, { useEffect } from 'react'
import "../css/pageCss/LandingPage.css";
import logo from "../images/logo.png"
import CommonCard from '../components/CommonCard';
import GoalsAchievedSection from '../components/GoalsAchievedSection';
import AboutUsSection from '../components/AboutUsSection';
import ContributeSection from '../components/ContributeSection';
import UpcomingEventsSection from '../components/UpcomingEventsSection';
import TestimonialSection from '../components/TestimonialSection';
import Navbar from './Navbar';
import volunteer from "../images/volunteer-common-card.png"
import donate from "../images/donate-common-card.png"
import event from "../images/event-common-card.png"

export default function LandingPage() {
let colors = ["#38B6FF" , "#36AB9C" , "#683998" , "#ED0E64" , "#F6AC0D" , "#F5F5F6"];
function moment(e){
    let moveX = e.clientX;
    let moveY = e.clientY;
    let circle = document.createElement("span");
    let randomNum = Math.floor(Math.random()*colors.length);
    let circleColor = colors[randomNum];
    let size = Math.random()*35;
    circle.style.width = size + "px";
    circle.style.height = size*0.4 + "px";
    circle.style.backgroundColor = circleColor;
    circle.style.top = moveY+"px";
    circle.style.left = moveX+"px"
    e.target.appendChild(circle)
    setTimeout(()=>{
        circle.remove();
    },500)
}

    useEffect(() => {
        window.scrollTo(0,0)
    },[])
    return (
        <>
            <div className='home-page-landing-section' id='main' onMouseMove={moment}>
                <img src={logo} alt="" className='home-page-landing-section-logo' />
                <h3>Welcome To</h3>
                <h1>HAPPY FAMILY</h1>
                <p>A dream to provide everything to everyone with love. We are the platform to serve the required thing to those who are not able or not blessed as us by the God.</p>
                <p>Bet the part of our journey and be the reason for someones smile!!</p>
                <div className="drop-animation"></div>
            </div>
            <div className="common-flex-section">
                <CommonCard image = {volunteer} title = "Volunteer" description="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book"/>
                <CommonCard image = {donate} title = "Donate To US" description="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book"/>
                <CommonCard image = {event} title = "Events" description="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book"/>
            </div>
            <div className="home-page-quality-section common-flex-section">
                <div className="quality-element">
                    <img src={logo} alt="" />
                    <h3>10K+</h3>
                    <h5>Children Helped</h5>
                </div>
                <div className="quality-element">
                    <img src={logo} alt="" />
                    <h3>1K+</h3>
                    <h5>Women Helped</h5>
                </div>
                <div className="quality-element">
                    <img src={logo} alt="" />
                    <h3>100+</h3>
                    <h5>Volunteer Members</h5>
                </div>
            </div>
            <GoalsAchievedSection/>
            <AboutUsSection/>
            <ContributeSection/>
            <UpcomingEventsSection/>
            <TestimonialSection/>
        </>
    )
}
